.Button {
    display: inline-block;
   padding: 10px 20px;
   border-radius: 4px;
   border: 1px solid #ccc;
   margin-right: 15px;
   color:#000;
   text-transform: uppercase;
   font-weight: bold;
   font-size: 12px;

    
}
.Button:focus {
    outline: none;
}
.Button:active{
    box-shadow: inset 2px 2px 1px rgba(0, 0, 0, .3);
}
.Button:disabled{
    background:#ccc;
    color: #000;
    cursor: not-allowed;
}
.Error{
    background:rgba(221, 53, 76, 1);
}
.success{
    background: rgba(161, 240, 69, 1)
}
.primary {
    background:#2884f6;
    color: #fff;
}